@import "./variables.scss";
@import "./blocks/page.scss";

html {
    box-sizing: border-box;
}

.header-block__black {
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    color: $white;
    background-color: $black;
}

.header-block__paragraph {
    font-size: 12px;
    line-height: 15px;
    margin-top: 0;
    padding-top: 7px;
    padding-bottom: 7px;
    letter-spacing: 2px;
}

.header-block__navline {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 23px;
    margin-left: 11px;
}

.header-block__nav-list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
}

.header-block__nav-link {
    color: $black;
    text-decoration: none;
    text-transform: uppercase;
}

.header-block__nav-link {
    color: $black;
    font-size: 14px;
    line-height: 21px;
    text-decoration: none;
    letter-spacing: 1px;
    margin-right: 22px;
}

.header-block__users {
    margin-right: 25px;
}

.header-block__img {
    background-image: url("/img/header_img_fon@1x.jpg");
    width: 100%;
    background-color: darkgrey;
    background-size: cover;
    background-repeat: no-repeat;
}

.header-block__heading {
    font-size: 44px;
    line-height: 51px;
    margin: 0;
    margin-bottom: 14px;
    margin-left: auto;
    margin-right: auto;
    inline-size: fit-content;
    padding-top: 45px;
}

.header-block__subtitle {
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 1px;
    margin: 0;
    margin-bottom: 22px;
    margin-left: auto;
    margin-right: auto;
    inline-size: fit-content;
    padding-top: 0;
}

.header-block__button {
    text-decoration: none;
    border: 1px solid $orange;
    border-radius: 35px;
    background-color: $orange;
    padding: 5px 37px 5px 37px;
    color: $white;
    font-size: 14px;
    line-height: 22px;
    display: inline-block;
    margin-bottom: 618px;
}

.header-block__main--button {
    inline-size: fit-content;
    margin-left: auto;
    margin-right: auto;
}

.header-block__svg {
    margin-right: 122px;
    margin-top: 18px;
    margin-bottom: 34px;
}

.header-block__partners {
    background-color: $black;
    margin-bottom: 52px;
}

.header-block__svgblock {
    inline-size: fit-content;
    margin-left: auto;
    margin-right: auto;
}

.title-block__catalog {
    margin: 0;
    margin-bottom: 12px;
    font-size: 32px;
    line-height: 36px;
    text-align: center;
}

.title-block__subtitle {
    margin: 0;
    margin-bottom: 25px;
    font-size: 15px;
    line-height: 19px;
    text-align: center;
    max-width: 927px;
    margin-left: auto;
    margin-right: auto;
}

.catalog-img__boards {
    background-image: url("/img/boards-catalog.png");
    background-repeat: no-repeat;
    padding: 283px 169px 267px 170px;
    margin-right: 20px;
}

.catalog-img__revs {
    background-image: url("/img/revs-catalog.png");
    background-repeat: no-repeat;
    padding: 283px 169px 267px 170px;
}

.catalog-img {
    display: flex;
    justify-content: center;
}

.catalog-img__subtitle {
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    margin: 0;
}

.catalog-img__title {
    font-size: 25px;
    line-height: 30px;
    text-align: center;
    margin: 0;
    margin-bottom: 14px;
}

.color {
    color: $white;
}
